.login-container{
    min-height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
    .login-wrapper{
        background-color: var(--base-color);
        width: 90vw;
        height: 90vh;
        .login-banner-area {
            background: var(--primary-color);
            height: 100%;
            color: var(--text-max);
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding: 20px 40px;
            background-position: center center;
            background-repeat: no-repeat;
            background-size: cover;
            .header-section {
                img {
                    max-width: 100px;
                }
            }
            .footer-section {
                h3{
                    text-align: right;
                    border-bottom: 1px solid var(--text-color);
                    line-height: 0.1em;
                    span {
                        background:var(--primary-color);
                        padding: 0 10px 0 25px;
                        font-size: var(--fs-md);
                        font-weight: 600;
                    }
                }
            }
        }
        .back-btn{
            outline: 0;
            border: 0;
            cursor: pointer;
            border-radius: 4px;
            transition: all 0.2s ease;
            height: 2.5rem;
            width: 2.5rem;
            background-color: transparent;
            svg {
                stroke: var(--text-max);
            }
            &:hover{
                background-color: var(--hover-color);
            }
        }
        .login-area {
            background-repeat: no-repeat;
            background-position: right 30px top 20px;
            height: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            .forget-password{
                font-weight: 500;
                color: var(--primary-color);
            }
            .fancy-heading{
                color: var(--primary-color);
            }
        }
    }
}